body {
    margin: 0px;
    font-Family: 'Montserrat';
    color: #FF6060;
}

* {
    transition: all 0.5s cubic-bezier(.53, -0.94, .18, 2.03);
}

ul {
    height: 100%;
    list-style: none;
    margin: 0;
    display: flex;
    align-items: center;
}

a {
    text-decoration: none;
}